body, html {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  direction: rtl;
  background-color: #f9f9f9;
  color: #222;
}

/* ============ NAVBAR ============ */
.navbar {
  position: sticky;
  top: 0;
  background: white;
  padding: 1rem 2rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
}

.navbar-logo a {
  font-weight: bold;
  font-size: 1.2rem;
  color: #3361f6;
  text-decoration: none;
}

.navbar-links a {
  margin-right: 1.5rem;
  color: #333;
  text-decoration: none;
  font-size: 0.95rem;
}

.navbar-links a.active {
  font-weight: bold;
  color: #3361f6;
}

/* ============ PAGE CONTAINERS ============ */
.page-container {
  max-width: 960px;
  margin: 2rem auto;
  padding: 0 1.5rem;
}

/* ============ HEADINGS ============ */
h1, h2, h3 {
  font-weight: 700;
  margin-bottom: 0.5rem;
}

p {
  line-height: 1.6;
  margin-bottom: 1rem;
}

/* ============ BUTTONS ============ */
button,
.primary-button {
  background: #3361f6;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  font-size: 1rem;
  cursor: pointer;
}

button:hover,
.primary-button:hover {
  background: #264dcc;
}

.secondary-link {
  font-size: 0.9rem;
  color: #3361f6;
  text-decoration: underline;
}

/* ============ LISTS ============ */
ul {
  list-style: none;
  padding: 0;
}

ul li::before {
  content: "‚≠ê";
  margin-left: 0.5rem;
}

/* ============ RESPONSIVENESS ============ */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .navbar-links {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    margin-top: 0.5rem;
  }

  .page-container {
    padding: 1rem;
  }
}

/*=============Landing Container Additions======*/
.landing-container {
  padding-bottom: 80px;
}

.hero-title {
  font-size: 2.5rem;
  font-weight: 700;
}

.hero-image {
  width: 100%;
  margin-top: 1.5rem;
  border-radius: 12px;
}